import React,{ Component } from 'react'
import {BrowserRouter as Router,Route,Link,withRouter} from 'react-router-dom'//

import './Article.less'

class ArticleHeader extends Component{
	constructor(props){
		super(props)
		this.state={
			isCurrent:0,
			category:["","","","",""],
		}
	}
	render(){
	    return (
	      <div className="articleContainer">
	         {this.state.category.map((item,i)=>
	           <span className={`nav ${this.state.isCurrent===i?'active':''}`} key={i}>{item}</span>
	         )}
	      </div>
	    )
    }
}

class Article extends Component{
	constructor(props){
		super(props)
		this.state={

		}
	}
	render(){
		return(
          <div>
            <ArticleHeader />
            <button onClick={()=>{this.props.history.push('/Lists')}}></button>
          </div>    
		)
	}
}


export default withRouter(Article)


